import { useTranslation } from "react-i18next";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../PlateformUser/PlateformUser.module.css";
const SelectOtherFeesCard = ({
  fees,
  selectedId,
  onSelect,
  selectList,
  isMultiSelect,
  onMultiSelect,
}) => {
  const { t } = useTranslation();
  const onSelectStaff = () => {
    onSelect(fees);
  };
  const onSelectMultipleStaff = () => {
    onMultiSelect(fees);
  };
  return (
    <>
      {isMultiSelect ? (
        <div
          className={style.assign_show_list_each}
          onClick={onSelectMultipleStaff}
          style={{
            border: "0.5px solid #cacaca",
            marginBlock: "0.4rem",
            borderRadius: "6px",
            padding: "0.8rem 1rem",
          }}
        >
          {selectList?.includes(fees?._id) ? (
            <img
              src={`${assestsUserAdmissionUrl}/check.svg`}
              alt="icon"
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
            />
          ) : (
            <img
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
              src={`${assestsUserAdmissionUrl}/uncheck.svg`}
              alt="icon"
            />
          )}
          <div className={style.assign_each_paragraph}>
            <h6>{fees?.other_fees_name ?? ""}</h6>
            <p>
              {fees?.fees_heads?.[0]?.head_name ?? ""}
              {": "}
              {fees?.payable_amount ?? ""}
            </p>
            {fees?.fee_structure?.unique_structure_name ? (
              <p>
                {t("fees_structure")} {": "}
                {fees?.fee_structure?.unique_structure_name ?? ""}
              </p>
            ) : (
              <p>
                {t("bank_name")} {": "}
                {fees?.bank_account?.finance_bank_account_name ?? ""}
              </p>
            )}
          </div>
        </div>
      ) : (
        <div
          className={style.assign_show_list_each}
          style={
            selectedId === fees?._id
              ? {
                  backgroundColor: "rgba(75, 26, 133, 0.1)",
                  border: "0.5px solid #cacaca",
                  marginBlock: "0.4rem",
                  borderRadius: "6px",
                  padding: "0.8rem 1rem",
                }
              : {
                  border: "0.5px solid #cacaca",
                  marginBlock: "0.4rem",
                  borderRadius: "6px",
                  padding: "0.8rem 1rem",
                }
          }
          onClick={onSelectStaff}
        >
          <img
            src={`${assestsUserFinanceUrl}/fees-card.svg`}
            loading="lazy"
            alt="avatar"
            style={{
              width: "2.5rem",
              height: "2.5rem",
              borderRadius: "0",
            }}
          />
          <div className={style.assign_each_paragraph}>
            <h6>{fees?.other_fees_name ?? ""}</h6>
            <p>
              {fees?.fees_heads?.[0]?.head_name ?? ""}
              {": "}
              {fees?.payable_amount ?? ""}
            </p>
            {fees?.fee_structure?.unique_structure_name ? (
              <p>
                {t("fees_structure")} {": "}
                {fees?.fee_structure?.unique_structure_name ?? ""}
              </p>
            ) : (
              <p>
                {t("bank_name")} {": "}
                {fees?.bank_account?.finance_bank_account_name ?? ""}
              </p>
            )}
          </div>
        </div>
      )}
    </>
  );
};

export default SelectOtherFeesCard;
