import style from "../../../PlateformUser/PlateformUser.module.css";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import SelectOtherFeesCard from "./SelectOtherFeesCard";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useFinanceAllOtherFeesList } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
const SelectOtherFees = ({
  title,
  onClose,
  selectedId,
  onSelectStaff,
  fid,
  isMultiSelect,
  onMultiSelect,
  selectList,
  onDone,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { financeAllOtherFeesList, financeAllOtherFeesListLoading } =
    useFinanceAllOtherFeesList({
      data: {
        fid: fid,
        page: page,
        limit: 10,
        search: search,
        type: "",
      },
      skip: !fid,
    });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeAllOtherFeesList?.list?.length) {
      let obj = nextPage(financeAllOtherFeesList?.list?.length);
      setState(() => obj);
    }
  }, [financeAllOtherFeesList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !financeAllOtherFeesListLoading &&
      financeAllOtherFeesList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onSelect = (value) => {
    if (selectedId === value?._id) {
    } else {
      onSelectStaff(value);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t(title ?? "select_miscellaneous_fees")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              {isMultiSelect && (
                <CreateButton
                  label={t("select")}
                  count={selectList?.length}
                  onAction={onDone}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              )}
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {financeAllOtherFeesList?.list?.map((fees, index) =>
            financeAllOtherFeesList?.list?.length === index + 1 ? (
              <div key={fees?._id} ref={ref}>
                <SelectOtherFeesCard
                  fees={fees}
                  selectedId={selectedId}
                  onSelect={onSelect}
                  selectList={selectList}
                  isMultiSelect={isMultiSelect}
                  onMultiSelect={onMultiSelect}
                />
              </div>
            ) : (
              <SelectOtherFeesCard
                fees={fees}
                key={fees?._id}
                selectedId={selectedId}
                onSelect={onSelect}
                selectList={selectList}
                isMultiSelect={isMultiSelect}
                onMultiSelect={onMultiSelect}
              />
            )
          )}
          {financeAllOtherFeesListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            financeAllOtherFeesList?.list?.length === 0 && (
              <EmptyMessage
                title="No miscellaneous fees found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            financeAllOtherFeesList?.list?.length === 0 && (
              <EmptyMessage
                title="No miscellaneous fees."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default SelectOtherFees;
